import { type ChangeEvent, type ReactElement } from 'react'

import {
  Box,
  Card,
  CardHeader,
  Checkbox,
  Divider,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography
} from '@mui/material'

interface ObjectWithId extends Object {
  id: number
}

export interface Property {
  header: string
  property: string
}

interface SelectingTableProps<ItemType> {
  items: ItemType[]
  title: ReactElement
  selectedItems: number[]
  setSelectedItems: React.Dispatch<React.SetStateAction<number[]>>
  BulkActions: ReactElement
  properties: Property[]
  handleClick: (idx: number) => void
}

const SelectingTable = <ItemType extends ObjectWithId>({
  items,
  title,
  selectedItems,
  setSelectedItems,
  BulkActions,
  properties,
  handleClick
}: SelectingTableProps<ItemType>) => {
  const selectedSomeApplicants = selectedItems.length > 0 && selectedItems.length < items.length
  const selectedAllApplicants = selectedItems.length === items.length
  const selectedBulkActions = selectedItems.length > 0

  const handleSelectAllApplicants: React.ChangeEventHandler<HTMLInputElement> = event => {
    setSelectedItems(event.target.checked ? items && items.map(items => items.id) : [])
  }

  const handleSelectOneApplicant = (_event: ChangeEvent<HTMLInputElement>, itemId: number): void => {
    if (!selectedItems.includes(itemId)) {
      setSelectedItems((prevSelected: number[]) => [...prevSelected, itemId])
    } else {
      setSelectedItems((prevSelected: number[]) => prevSelected.filter(id => id !== itemId))
    }
  }

  return (
    <Card>
      {selectedBulkActions ? (
        <Box height="55px">
          <Box flex={1} p={1}>
            {BulkActions}
          </Box>{' '}
        </Box>
      ) : (
        <CardHeader title={title} />
      )}

      <Divider />
      <TableContainer sx={{ height: '50vh' }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell padding="checkbox">
                <Checkbox
                  color="primary"
                  checked={selectedAllApplicants}
                  indeterminate={selectedSomeApplicants}
                  onChange={handleSelectAllApplicants}
                />
              </TableCell>
              {properties.map(property => {
                return <TableCell>{property.header}</TableCell>
              })}
            </TableRow>
          </TableHead>
          <TableBody>
            {items &&
              items.map((item, i) => {
                const isItemSelected = selectedItems.includes(item.id)
                return (
                  <TableRow
                    hover
                    key={String(item.id)}
                    id={String(item.id)}
                    onClick={() => {
                      handleClick(i)
                    }}
                  >
                    <TableCell padding="checkbox">
                      <Checkbox
                        color="primary"
                        checked={isItemSelected}
                        onClick={e => e.stopPropagation()}
                        onChange={(event: ChangeEvent<HTMLInputElement>): void =>
                          handleSelectOneApplicant(event, item.id)
                        }
                        value={isItemSelected}
                      />
                    </TableCell>

                    {properties.map((property, index) => {
                      return (
                        <TableCell sx={{ cursor: 'pointer' }}>
                          <Typography variant="body1" fontWeight={index === 0 ? 'bold' : 'normal'} gutterBottom noWrap>
                            {
                              // TODO : item 타입 좀 더 잘 처리하기
                              // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                              // @ts-ignore
                              item[property.property]
                            }
                          </Typography>
                        </TableCell>
                      )
                    })}
                  </TableRow>
                )
              })}
          </TableBody>
        </Table>
      </TableContainer>
    </Card>
  )
}

export default SelectingTable
